}

.label-component {
  background-color: rgba(0, 0, 0, 0.16);
  width: 100%;
  text-align: center;
  cursor: pointer;
}

.label {
  font-size: 15px;
  font-weight: 400;
  font-style: italic;
  padding: 5px 0;
  margin-top: 0;
}

.label-component.label {
  margin: 2px 0 6px 0;
  font-size: 15px;
}

.avatar {
  height: 172px;
  width: 172px;
  margin: 12px 0 8px 0;
  border: 0.5px solid black;
  border-radius: 50%;
  box-shadow: 0px 0px 49px -24px rgba(1, 1, 1, 0.4);
  overflow: hidden;
}
@media (max-width: 590px) {
  .avatar {
    width: 150px;
    height: 150px;
  }
}
@media (max-width: 380px) {
  .avatar {
    width: 130px;
    height: 130px;
  }
}

.pieces {
  flex: none;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  background-color: #deac23;
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: 0 0;
}

.personal-details p {
  line-height: 20px;
}

.personal-details p span:first-child {
  margin-left: 15px;
  width: 95px;
  display: inline-block;
}

.personal-details a {
  color: white;
  text-decoration: none;
}

@media (max-width: 880px) {
  .personal-details {
    display: flex;
  }
  .personal-details p span:first-child {
    margin-left: 0;
  }
  .personal-details p span  {
    margin: auto;
    margin-bottom: 4px;
  }
}
@media (max-width: 436px) {
  .personal-details p span {
    margin-right: 25px;
  }
  .personal-details p span:first-child {
    width: auto;
  }
}
@media (max-width: 390px) {
  .personal-details p span {
    margin-right: 1px;
  }
  .personal-details p:last-child {
    width: 100px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .personal-details p span.email a {
    overflow: hidden;

  }
}

.about-me {
  width: 500px;
  font-weight: 350;
  line-height: 1.6;
}
@media (max-width: 580px) {
  .about-me {
